_details_wrapper .mydetails_form_wrapper  .file_uploader { padding: 0 10px; }\r\n.my_details_wrapper .mydetails_form_wrapper  .file_uploader .mydetails_ac_lebel { font-size: 16px;\r\n    text-transform: uppercase;\r\n    font-family: \"HelveticaRegular\";\r\n    color: #111214;\r\n    margin: 10px 0;\r\n    display: block;}\r\n.my_details_wrapper .mydetails_form_wrapper  .file_uploader .inner_wrapper{ display: flex; align-items: center; }\r\n.my_details_wrapper .mydetails_form_wrapper  .file_uploader .inner_wrapper .upoader{ width: 85px; height: 85px; border: 1px solid #ddd; border-radius: 2px; padding: 5px; margin-bottom: 0; }\r\n.my_details_wrapper .mydetails_form_wrapper  .file_uploader .inner_wrapper .upoader img{ max-width:100%; }\r\n.my_details_wrapper .mydetails_form_wrapper  .file_uploader .inner_wrapper .change_image{ font-size: 20px;\r\n    font-family: \"HelveticaBold\";\r\n    color: #00163a;\r\n    text-transform: capitalize;\r\n    width: 200px;\r\n    padding: 5px;\r\n    background: linear-gradient(180deg, #80d7ff, #0081de);\r\n    margin-left: 20px;  }\r\n@media screen and (max-width: 991px){\r\n.my_details_wrapper .mydetails_form_wrapper{ padding: 16px;}\r\n.my_details_wrapper .mydetails_form_wrapper .forms { width: 100%; margin: 1% 0; }\r\n}\r\n@media screen and (max-width:840px){\r\n.my_details_wrapper .mydetails_form_wrapper .button_action button { width: 49%; }\r\n}\r\n.clickable{\r\n    cursor: pointer;\r\n}\r\n\r\n/*# sourceMappingURL=data:application/json;base64, */"