m_list .mat-list-item span { height: auto; font-size: 14px; font-family: \"HelveticaBold\"; color: #30373d; text-transform: uppercase;padding: 7px 0; word-break: break-word;}\r\n.hospital_items .hospital_right .item_list .mat-list-item span { min-width: 190px; height: auto; align-items: inherit; }\r\n.hospital_items .hospital_right .item_list .mat-list-base .mat-list-item .mat-list-item-content { padding: 0; }\r\n.my_details_wrapper .mat_paginator{ background: #105d9f; color: #fff; display: flex; justify-content: center; align-items: center; margin: 0 16px; }\r\n@media screen and (max-width: 1500px){\r\n  .hospital_items .hospital_right .title { font-size: 18px; }\r\n}\r\n@media screen and (max-width: 991px){\r\n.my_details_wrapper .mydetails_form_wrapper{ padding: 16px;}\r\n.my_details_wrapper .mydetails_form_wrapper .forms { width: 100%; margin: 1% 0; }\r\n}\r\n@media screen and (max-width:840px){\r\n.my_details_wrapper .mydetails_form_wrapper .button_action button { width: 49%; }\r\n}\r\n\r\n/*# sourceMappingURL=data:application/json;base64, */"